

  .App {
    display: grid; 
    grid-template-columns: 1fr 2fr 0.6fr; 
    grid-template-rows: 0.5fr 6.1fr 0.4fr; 
    grid-gap: 10px 10px; 
    gap: 10px 10px; 
    grid-template-areas: 
      "navbar navbar packages"
      "details details packages"
      "footer footer packages"; 
      height: 100%;
  }

  .details { grid-area: details;}
  .navbar { grid-area: navbar;}
  .footer { grid-area: footer;}
  .packages { grid-area: packages;}

  * {
    margin: 0px; 
    padding: 0px; 
    box-sizing: border-box;
    font-family: 'Roboto Mono', monospace;
  }
  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }
  html, body, #root, #app > div {
    height: 100%;
    background-color: #2b2d42;
  }

.copyright {
    text-decoration: none;
    color: black;
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 14px;
    opacity: .7;
}

.copyright:hover {
    opacity: 1;
}

.footer {
    display: flex;
    justify-content: flex-start;
    padding-left: 2em;
    align-items: center;
    background: #2b2d42;
}
#navbar {
  background: #2b2d42;
  color: #edf2f4;
  display: flex;
  align-items: center;
  line-height: 60px;
  z-index: 10;
  }
  
  .logo {
    float: left;
    margin-left: 28px;
    font-size: 1.5em;
    height: 60px;
    letter-spacing: 1px;
    text-transform: uppercase;
  } 

  .logo p {
    text-decoration: none;
    color: #edf2f4;
    font-weight: 700;
  }
  
  
  
  
.details {
  overflow: auto;
}
.pkgList {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: wrap;
}

.packages {
  overflow-y: auto;
  box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
}


.pkgName {
    color: #edf2f4;
    text-decoration: none;
    padding: 12px;
    background-color: #4a4e69;
    cursor: pointer;
    /* fill effect */
    background-image: linear-gradient(to right, #1a1a1a 50%, transparent 50%);
    background-size: 200% 1.5px;
    background-repeat: no-repeat;
    background-position: 100% 100%;
    transition: background-position .4s ease-out;
    border-radius: 5px;
  }

  .pkgName:hover {
    background-position: 0% 100%;
    color: #e63946;
  }

  .pkgName:nth-child(Odd) {
  background-color: #2b2d42;
}

.pkgName.active {
    background-color: #1a1a1a;
    color: #e63946;
}

::-webkit-scrollbar{
	width: 8px;
}

::-webkit-scrollbar-track-piece{
	background-color: #FFF;
}

::-webkit-scrollbar-thumb{
	background-color: #CBCBCB;
	outline: 2px solid #FFF;
	outline-offset: -2px;
	border: .1px solid #B7B7B7;
}

::-webkit-scrollbar-thumb:hover{
	background-color: #909090;
}
.details {
    background-color: #bdd5ea;
    color: #333;
    margin: 10px;
    border-radius: 10px;
    font-size: 20px;
}

.header {
    padding: 1em;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;    
    height: 60px;
    font-weight: 600;
    font-size: 24px;
    background-color: #8d99ae;
}

.header > span {
    margin-right: 25px;
}

.return-button {
    cursor: pointer;
    background-color: #333;
    color: #e63946;
    padding: 5px;
    border-radius: 50px;
    font-size: 16px;
    padding: 10px;
}

.return-button:hover {
    color: #e63946;
}
.empty {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-column: 1/4;
    background-color: #FFF;
}

.all {
    display: flex;
    flex-wrap: wrap;
    background: #2b2d42 !important;
}

.package-card {
    flex: 1 0 21%;
    background-color: #8d99ae;
    padding: 2em;
    margin: 10px;
    border-radius: 5px;
    border: 1px solid #FFF;
    position: relative;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    cursor: pointer;
}

.package-card:hover {
    opacity: .8;   
}

.package-card:hover .card-detail {
    color: #e63946;
}

.card-detail {
    color: black;
    font-weight: 600;
}

.box-svg {
    position: absolute;
    top: 5px;
    right: 5px;
}

.header > h3 {
    cursor: pointer;
    padding-left: 10%;
}

.dependency.disabled {
    display: none;
}

.dependency-clickable {
    cursor: pointer;    
}

.dependency-clickable:hover {
    color: #e63946;
}

.details-container {
    padding: 2em;
    display: flex;
    flex-wrap: wrap;
}

.dependencies-container {
    padding: 2em;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
}

.dependencies {
    flex: 1 0 50%;
    padding-bottom: 10px;
}

.reverse-dependencies {
    flex: 1 0 50%;
}

.pkg {
    /* border-bottom: 1px solid; */
    flex: 1 0 50%;
    padding-left: 0;
    border-radius: 5px;
}

.pkg:last-child {
    border-bottom: 0;
}

.pkg-keys {
    font-weight: 600;
    letter-spacing: 1.2px;
}

.pkg-values {
    padding-bottom: 1em;
    word-break: break-word;
    width: 90%;
}

.disabled {
    cursor:not-allowed;
    opacity: .5;
}

.close-file {
    position: absolute;
    top: 40px;
    right: 20%;
    fill: #e63946;
    z-index: 100;
    display: flex;
    cursor: pointer;
}

.close-file > p {
    padding-right: 10px;
}

.close-file:hover {
    fill: #FFF;
    color: #FFF;
}
.select-file {
    margin-top: 2em;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 50px;
    width: 150px;
    border-radius: 15px;
    background: #e63946;
    cursor: pointer;
    position: relative;
}

.select-file:hover {
    color: #FFF;
    fill: #FFF;
}

.add-icon {
    position: absolute;
    top: -5px;
    right: 0;
}
