#navbar {
  background: #2b2d42;
  color: #edf2f4;
  display: flex;
  align-items: center;
  line-height: 60px;
  z-index: 10;
  }
  
  .logo {
    float: left;
    margin-left: 28px;
    font-size: 1.5em;
    height: 60px;
    letter-spacing: 1px;
    text-transform: uppercase;
  } 

  .logo p {
    text-decoration: none;
    color: #edf2f4;
    font-weight: 700;
  }
  
  
  
  